import { Fragment, MouseEvent, useEffect, useState } from "react";
import { Avatar, IconButton } from "@mui/material";

import { DropDown, DropUp, Hamburger, User, WorkSpace } from "Assets/Icons";
import { useAppState, useGlobal } from "Contexts/App";
import { ImageBox } from "Components/Common";

import {
  BrandSx,
  HamburgerButton,
  HeaderDown,
  HeaderEnd,
  HeaderStart,
  HeaderUp,
  HeaderWrapper,
  HeaderWrapperSX,
  ProfileAvatarSx,
  ProfileButtonSX,
  WorkSpaceLabel,
  WorkSpaceToggler,
} from "./LayoutComponents";

import AccountMenu from "./AccountMenu";
import WorkSpaceSwitcher from "./WorkSpaceSwitcher";
import BusinessSwitcher from "./BusinessSwitcher";
import { userInitials } from "Utils";

const Header = () => {
  const { isMobile, isTablet } = useGlobal();
  const { headerContent, workSpaceLabel } = useAppState();
  const [workSpaceEl, setWorkSpaceEl] = useState<null | HTMLElement>(null);
  const [profileEl, setProfileEl] = useState<null | HTMLElement>(null);
  const [showAccountMenu, setShowAccountMenu] = useState<boolean>(false);
  const [showBusinessSwitcher, setShowBusinessSwitcher] =
    useState<boolean>(false);
  const openWorkSpace = Boolean(workSpaceEl);
  const openProfileMenu = Boolean(profileEl);
  const [randomAvatar, setRandomAvatar] = useState<boolean | undefined>();
  const [randomUname, setRandomUname] = useState<boolean | undefined>();
  const userAvatar: string = randomAvatar ? "/static/images/jpg/u1.jpg" : "";
  const userFullName: string = randomUname ? "User One" : "";

  useEffect(() => {
    if (randomAvatar === undefined) {
      setRandomAvatar(Math.random() < 0.3);
    }
    if (randomUname === undefined) {
      setRandomUname(Math.random() < 0.3);
    }
  }, [randomAvatar, randomUname]);

  return (
    <Fragment>
      <HeaderWrapper sx={HeaderWrapperSX}>
        {isMobile && (
          <HeaderUp>
            <HamburgerButton
              type="button"
              disableRipple={true}
              disableFocusRipple={true}
              disableTouchRipple={true}
              onClick={() => setShowAccountMenu(true)}
            >
              <Hamburger />
            </HamburgerButton>
            <IconButton
              sx={{ ...BrandSx, m: 0 }}
              onClick={() => setShowBusinessSwitcher(true)}
            >
              <ImageBox
                src="/static/images/jpg/honda-logo.jpg"
                alt="brand-image"
              />
            </IconButton>
          </HeaderUp>
        )}
        {isMobile && <HeaderDown>{headerContent}</HeaderDown>}
        {isTablet && <HeaderStart>{headerContent}</HeaderStart>}
        {isTablet && (
          <HeaderEnd>
            <WorkSpaceToggler
              onClick={(event: MouseEvent<HTMLElement>) => {
                setWorkSpaceEl((workSpaceEl) =>
                  workSpaceEl === null ? event.currentTarget : null
                );
              }}
              sx={{
                borderRadius: openWorkSpace ? "8px 8px 0 0" : 2,
                boxShadow: openWorkSpace
                  ? "0px 6px 8px #00000026 !important"
                  : "none",
              }}
            >
              <WorkSpace />
              <WorkSpaceLabel>{workSpaceLabel}</WorkSpaceLabel>
              {openWorkSpace ? <DropUp /> : <DropDown />}
            </WorkSpaceToggler>
            <IconButton
              onClick={(event: MouseEvent<HTMLElement>) => {
                setProfileEl(event.currentTarget);
              }}
              sx={{
                ...ProfileButtonSX,
                ...(Boolean(profileEl)
                  ? {
                      boxShadow: "inset 0 0 0 3px #12b0ee",

                      "& .MuiAvatar-root": ProfileAvatarSx,
                    }
                  : {}),
              }}
            >
              {userFullName !== "" ? ( // ? check for userFullName
                userAvatar !== "" ? ( // ? check for userAvatar
                  <Avatar alt={userFullName} src={userAvatar} /> // ? display avatar image
                ) : (
                  <Avatar {...userInitials(userFullName)} /> // ? display users initials
                )
              ) : (
                <User /> // ?  display user icon
              )}
            </IconButton>
          </HeaderEnd>
        )}
      </HeaderWrapper>
      {openProfileMenu && (
        <AccountMenu
          open={openProfileMenu}
          anchorEl={profileEl}
          doClose={() => setProfileEl(null)}
        />
      )}
      <WorkSpaceSwitcher
        open={openWorkSpace}
        anchorEl={workSpaceEl}
        doClose={() => setWorkSpaceEl(null)}
      />
      {isMobile && (
        <AccountMenu
          open={showAccountMenu}
          doClose={() => setShowAccountMenu(false)}
        />
      )}
      {isMobile && (
        <BusinessSwitcher
          open={showBusinessSwitcher}
          doClose={() => setShowBusinessSwitcher(false)}
        />
      )}
    </Fragment>
  );
};

export default Header;
